
import { reactive, toRefs } from "vue";
import inputForm from "./form";

export default {
  name: "account-record-login--Info",
  props: {
    activeItem: { type: Object, required: true },
  },
  setup() {
    const data = reactive({
      options: [
        {
          value: true,
          label: "Util.yes",
          icon: "fas fa-check",
          color: "success",
        },
        {
          value: false,
          label: "Util.no",
          icon: "fas fa-ban",
          color: "danger",
        },
      ],
    });

    const refData = toRefs(data);

    return {
      ...refData,
      inputForm,
    };
  },
};
